.App {
  text-align: center;
}

h1 {
  font-size: 3em;
}

.heading-movies {
  color: red;
  text-align: left;
  -webkit-animation: showup 2s;
          animation: showup 2s;
  position: relative;
  margin-bottom: -20px;
  margin-left: 160px;
}

@-webkit-keyframes showup {
    from { top: 30px; }
    to   { top: 0; }
}

@keyframes showup {
    from { top: 30px; }
    to   { top: 0; }
}

.box {
    width: 100px;
    height: 100px;
    -webkit-transition: width 1s, height 1s;
    -o-transition: width 1s, height 1s;
    transition: width 1s, height 1s;
}

.box:hover {
    width: 200px;
    height: 200px;
}



